import React, { useState, useRef } from 'react';
import styled from 'styled-components';
import MapContainer from './MapContainer';

const StoreSectionBlock = styled.div`
background:#f8f8f8;
  padding:20px 0 ;
  button { background:#ffbc0d; color:#333;
    border:none; outline:none;
    font-weight:bold;
    border-radius:50px; 
    padding:20px 30px;
    font-size:15px;
}
  .storeFind { 
    margin-bottom:35px; 
    .storeIcon { display: flex;
        justify-content:center;
        padding:30px 0 ;
        div { width:10%; margin:1%;
            background:#fff;
            border:1px solid #ccc;
            border-radius:15px; 
            text-align:center;
            padding:15px 0 ;
            img { width:70%;}
            p { font-size:15px; font-weight:bold}
        }
    }
    .storeMap { text-align:center;
        padding:15px 0 30px;
        input { width:60%; padding:20px 0;
            border-radius:50px;
            border:1px solid #ccc;
            text-indent:2.5em;
            margin:0 auto;
        }
    }
    
  }

`;

const StoreSection = () => {
    const [inputText, setInputText] = useState('');

    const elInput = useRef();

    const onChange = (e) => {
        setInputText(e.target.value)
    }

    const [place, setPlace] = useState('');
    
    const onSubmit = (e) => {
        e.preventDefault();
        setPlace(inputText);
        setInputText('');
        elInput.current.focus();
    }

    return (
        <StoreSectionBlock>
            <div className="storeFind">
                <div className="storeIcon row">
                    <div>
                        <img src="images/store_li01.png" alt="" />
                        <p>24시간</p>
                    </div>
                    <div>
                        <img src="images/store_li02.png" alt="" />
                        <p>맥드라이브</p>
                    </div>
                    <div>
                        <img src="images/store_li03.png" alt="" />
                        <p>맥딜리버리</p>
                    </div>
                    <div>
                        <img src="images/store_li04.png" alt="" />
                        <p>맥모닝</p>
                    </div>
                    <div>
                        <img src="images/store_li05.png" alt="" />
                        <p>주차</p>
                    </div>
                    <div>
                        <img src="images/store_li06.png" alt="" />
                        <p>디카페인커피</p>
                    </div>
                </div>
                
                <form className="storeMap row" onSubmit={onSubmit}>
                   <input placeholder="ex) OO역 or OO동 맥도날드를 검색해보세요!" onChange={onChange} value={inputText} ref={elInput} />
                   <button type="submit">검색</button>
                </form>
                <MapContainer searchPlace={place} />
            </div>
            
            
        </StoreSectionBlock>
    );
};

export default StoreSection;