import React, { useEffect } from 'react';
import styled from 'styled-components';

const MapContainerBlock = styled.div`
   .closeBtn { 
       img { max-width:100% }
   }
`;



const { kakao } = window; // 빠지면 안됨

const MapContainer = ({searchPlace}) => {
    
    useEffect(() => {
        let infowindow = new kakao.maps.InfoWindow({zIndex:1});
        var markers = [];
        const container = document.getElementById('myMap');
        const options = {
           center: new kakao.maps.LatLng(33.450701, 126.570667),
           level: 7
        };
        const map = new kakao.maps.Map(container, options);

        // 장소 검색 객체를 생성합니다
        var ps = new kakao.maps.services.Places();
        ps.keywordSearch(searchPlace, placesSearchCB); // 변수명 확인
        function placesSearchCB (data, status, pagination) {
        if (status === kakao.maps.services.Status.OK) {
           var bounds = new kakao.maps.LatLngBounds();
           for (var i=0; i<data.length; i++) {
               displayMarker(data[i]);    
               bounds.extend(new kakao.maps.LatLng(data[i].y, data[i].x));
           }
           map.setBounds(bounds);
         } 
       }

        // 지도에 마커를 표시하는 함수입니다
        function displayMarker(place) {
        let marker = new kakao.maps.Marker({
            map: map,
            position: new kakao.maps.LatLng(place.y, place.x) 
        });
        kakao.maps.event.addListener(marker, 'click', function() {
        infowindow.setContent('<div style="padding:5px;font-size:12px;">' + place.place_name + '</div>');
        infowindow.open(map, marker);
        });
       }
        // 현재위치에 포커싱 시키기 (geolocation으로 마커 표시하기)
       if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(function(position) {
            var lat = position.coords.latitude, // 위도
                lon = position.coords.longitude; // 경도
            var locPosition = new kakao.maps.LatLng(lat, lon),
                message = '<div class="closeBtn" style="padding:5px;">현재 나의 위치</div>';
            displayMarker1(locPosition, message);   // 함수 이름 바꾸기 
          });    
       } else {
           var locPosition = new kakao.maps.LatLng(33.450701, 126.570667),    
               message = '현재 위치를 사용할 수 없습니다.'
               
           displayMarker1(locPosition, message);
       }
        function displayMarker1(locPosition, message) {
        var marker = new kakao.maps.Marker({  
            map: map, 
            position: locPosition
        });
        var iwContent = message,
            iwRemoveable = true;
        var infowindow = new kakao.maps.InfoWindow({
            content : iwContent,
            removable : iwRemoveable
        });
        infowindow.open(map, marker);
        map.setCenter(locPosition);      
       }    


    
    }, [searchPlace]); //useEffect 끝

 

    return (
        <MapContainerBlock id='myMap' style={{
            width: '60%', 
            height: '500px',
            margin: '0 auto',
            borderRadius:'30px'

        }}></MapContainerBlock>
    );
}

export default MapContainer;