import React from 'react';
import DessertBg from './DessertBg';
import DessertItem from './DessertItem';
import styled from 'styled-components';
import mac from './mac.json'

const DessertSectionBlock = styled.div`
 .dessertListWrap { display:flex; flex-wrap:wrap}
`;
const dessertList = mac.part2;

const DessertSection = () => {
    return (
        <DessertSectionBlock>
            <DessertBg />
            <div className="dessertListWrap row">
                {
                    dessertList.map(
                        (data,index) => (
                            <DessertItem key={index} name={data.name} enname={data.enname} description={data.description} image={data.image} />
                        )
                    )
                }
            </div>
        </DessertSectionBlock>
    );
};

export default DessertSection;