import React, { useEffect } from 'react';
import Header from '../components/Header';
import Footer from '../components/Footer';
import DessertBg from '../components/DessertBg';
import styled from 'styled-components';

const DessertDetailBlock = styled.div`
.dessertInfo { padding:80px 0;  text-align:center;
    img { width:40%; border-radius:20px  }
    p:nth-child(1) { font-size:38px;
       font-weight:bold;
       margin-top:-30px;
   }
    p:nth-child(2) { font-weight:bold; color:#666;
       padding:30px 0 ;
   }
    p:last-child { font-weight:bold; color:#666;
       padding:50px 0 10px;
   }
`;


const DessertDetail = (props) => {
    const { location, history } = props;

    useEffect (
        () => {
            if (location.state === undefined){
                history.push('/')
            }
        }, []
    )
    if ( location.state ) {
        return (
            <div className="dessertDetailWrap">
                <Header />
                <DessertBg />
                <DessertDetailBlock>
                    <div className="dessertInfo row">
                        <p>{location.state.name}</p>
                        <p>{location.state.enname}</p>
                        <p><img src={location.state.image} alt={location.state.name} /></p>
                        <p>{location.state.description}</p>
                    </div>
                </DessertDetailBlock>
                <Footer />
            </div>
        );
    } else {
        return null;
    }
};

export default DessertDetail;