import React from 'react';
import {Link} from 'react-router-dom';
import styled from 'styled-components';

const BurgerItemBlock = styled.div`
   width:33.33%; 
   padding:40px 0;
   text-align:center;
   img { width:80%; border-radius:20px; }
   h2 { padding:20px 0 10px; font-size:22px; }
   p { font-size:15px; font-weight:bold;
    color:#777; 
}
   
 
`;

const BurgerItem = ({name, enname, description, image}) => {
 
    return (
        <BurgerItemBlock>
           <div className="thumbnail">
                    <Link to={{pathname:'/burger/detail', state:{name, enname,description, image}}}>
                        <img src={image} alt={name} />
                        <h2>{name}</h2>
                        <p>{enname}</p>
                    </Link>
                        
                </div>
            
        </BurgerItemBlock>
    );
};

export default BurgerItem;