import React from 'react';
import Header from '../components/Header';
import Footer from '../components/Footer';
import DessertSection from '../components/DessertSection';

const Dessert = () => {
    return (
        <div className="dessertWrap">
            <Header />
            <DessertSection />
            <Footer />
        </div>
    );
};

export default Dessert;