import React from 'react';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import './HomeSection.scss';
import  { IoIosArrowBack, IoIosArrowForward } from 'react-icons/io';
import styled from 'styled-components';


const Article = styled.div`
  position:relative; 
  .slick-arrow { position:absolute; top:50%; transform:translateY(-50%);
     width:70px; height:90px;
     background:rgba(0,0,0,0.3); color:rgba(255,255,255,0.5); 
     border:none; 
     padding:10px; 
     z-index:9999;
     cursor:pointer;
    &.slick-prev { left:0px;  border-radius:0 20px 20px 0;
        padding-left:5px;
    }
    &.slick-next { right:0px; border-radius:20px 0 0 20px;
        padding-left:5px; 
    }
  }
  
  .slick-dots { position:absolute; bottom:30px; left:50%; 
    transform:translate(-50%);
    li { display:inline-block;
        & + li { padding-left:10px; }
        button { width:15px; height:15px;
            border:none; border-radius:50%; 
            background:rgba(255,255,255,0.6);
            text-indent:-9999px; overflow:hidden;
        }
    }
    li.slick-active {
        button { background:#b32025; }
    }
  }

  .slide {
      position:relative;
       height:30vw !important;  
      background-size:cover;
      background-position:center;
      background-repeat:no-repeat;
  }
  .slide1 { background-image:url(images/main_slide01.png);}
  .slide2 {background-image:url(images/main_slide02.png);}
  .slide3 {background-image:url(images/main_slide03.png);}

  
  

`;

const HomeSection = () => {
    const settings = {
        autoplay:true,
        dots : true,
        infinite:true,
        speed:500,
        slidesToShow:1,
        slidesToScroll:1,
        prevArrow:<IoIosArrowBack />,
        nextArrow:<IoIosArrowForward />
    }
  
    return (
        <div className="homesecWrap">
            <Article className="article1">
                <Slider {...settings}>
                    <div className="slide slide1"></div>
                    <div className="slide slide2"></div>
                    <div className="slide slide3"></div>
                </Slider>
            </Article>

            <div className="article2 row">
                <h2>McDonald's LIVE</h2>
                <div className="foodWrap">
                    <div>
                        <img src="images/section_img01.png" alt="" />
                        <p>2020년까지 이어진 <br /> 1955년 오리지널의 맛!</p>
                        
                    </div>
                    <div>
                        <img src="images/section_img02.png" alt="" />
                        <p>겨울에 더 달콤한 한잔!<br />딸기 칠러</p>
                        
                    </div>
                    <div>
                        <img src="images/section_img03.png" alt="" />
                        <p>보랏빛 달콤함 타로파이</p>
                    </div>
                    <div>
                        <img src="images/section_img04.png" alt="" />
                        <p>빨대가 필요없는 뚜껑이를 만나보세요!</p>
                    </div>
                    <div>
                        <img src="images/section_img05.png" alt="" />
                        <p>언제나 가까이 맥드라이브와 함께</p>
                    </div>
                    <div>
                        <img src="images/section_img06.png" alt="" />
                        <p>1+등급 계란으로 더욱 신선하게! <br />에그 불고기 버거 세트 4,000원
                        </p>
                        
                    </div>
                </div>
            </div>


            
        </div>
    );
};

export default HomeSection;