import React from 'react';
import {HashRouter, Route} from 'react-router-dom';
import Home from './routes/Home';
import Login from './routes/Login';
import Join from './routes/Join';
import Burger from './routes/Burger';
import BurgerDetail from './routes/BurgerDetail';
import Brand from './routes/Brand';
import Dessert from './routes/Dessert';
import DessertDetail from './routes/DessertDetail';
import Store from './routes/Store';

const App = () => {
  return (
    <HashRouter>
      <Route path='/' exact={true} component={Home} />
      <Route path='/burger' exact={true} component={Burger} />
      <Route path='/burger/detail' component={BurgerDetail} />
      <Route path='/brand' component={Brand} />
      <Route path='/dessert' exact={true} component={Dessert} />
      <Route path='/dessert/detail' component={DessertDetail} />
      <Route path='/login' component={Login} />
      <Route path='/join' component={Join} />
      <Route path='/store' component={Store} />
      
    </HashRouter>
  );
};

export default App;
