const BurgerSection = () => {
    return (
        <BurgerSectionBlock>
            <BurgerBg />
            <div className="burgerListWrap row">
                {
                    burgerList.map(
                        (data,index) => (
                            <BurgerItem key={index} name={data.name} enname={data.enname} description={data.description} image={data.image} />
                        )
                    )
                }
            </div>
        </BurgerSectionBlock>
    );
};

export default BurgerSection;