import React from 'react';
import styled from 'styled-components';

const BrandFirstBlock = styled.div`
   h2 { padding:20px 0; font-size:26px; color:#333; }
   h3 { padding:20px 0 10px; font-size:21px; color:#333; }
   h4 { padding:5px 0 10px; font-size:21px; color:#333; }
   p { padding:5px 0; font-size:15px; font-weight:bold; color:#777;}
   
   .brandSection01 { padding:40px 0 60px; border-bottom:1px solid #999;
    p { font-size:16px;}
}
 .brandSection02 {
     padding:40px 0 60px;
     .brandImgWrap { display:flex; flex-wrap:wrap;  
        img { height:435px; }
        div { width:50%; }
        div:nth-child(2) {padding-left:20px; }
        div:nth-child(3) {padding-top:20px; }
        
    }
 }
`;

const BrandFirst = () => {
    return (
        <BrandFirstBlock>
           <div className="brandFirstWrap row">
               <div className="brandSection01">
                   <img src="images/brand_cont01.jpg" alt="맥도날드 소개" />
                   <h2>세계 1위의 푸드서비스 기업, 맥도날드</h2>
                   <p>전세계 120개국 3만 7천여 개의 매장에서 매일 6,900만명의 고객들에게 제품과 서비스를 제공하고 있는 맥도날드는 전세계인들이 사랑하는</p>
                   <p>퀵 서비스 레스토랑(QSR, Quick Service Restaurant)이자 세계 1위의 푸드서비스 기업으로, 고객에서 더 나은 경험을 제공함으로써</p>
                   <p>'고객이 가장 좋아하는 장소이자 음식을 즐기는 최고의 방법(Our Customer's Favorite Place and Way to Eat)'이 되기 위해 노력하고 있습니다.</p>
               </div>
               <div className="brandSection02">
                   <h2>한국 맥도날드의 첫 걸음</h2>
                   <div className="brandImgWrap">
                       <div>
                           <img src="images/brand_cont02.jpg" alt="" />
                           <h3>1988 서울 올림픽을 개최하며 서울이 세계 속에</h3>
                           <h4>우뚝 섰던 그때의 감동을 기억하십니까?</h4>
                           <p>159개국의 83,190명의 선수가 참가한 1988 서울 올림픽과 함께 맥도날드가</p>
                           <p>한국을 찾아왔습니다. 이후 맥도날드는 빅맥, 후렌치 후라이 그리고 해피밀과 같이</p>
                           <p>세계적으로 유명한 메뉴를 선보이며 한국 소비자들이 선호하는 즐거운 외식 공간으로</p>
                           <p>사랑을 받고 있습니다.</p>
                       </div>
                       <div><img src="images/brand_cont03.jpg" alt="" />
                          <h3>한국 첫 맥도날드 매장, 압구정점 그랜드 오프닝</h3>
                          <p>맥도날드는 가장 트렌디한 장소로 떠오르는 압구정동에 첫 레스토랑을 열었습니다.</p>
                          <p>수백 명의 고객들이 레스토랑 앞에 길게 줄을 늘어서며 맥도날드의 성공적인 시작을</p>
                          <p>함께 축하했고 이를 통해 맥도날드가 한국 시장에서 지속적으로 성장할 수 있다는</p>
                          <p>잠재력을 보여 주었습니다.</p>
                       </div>
                       <div><img src="images/brand_cont04.jpg" alt="" />
                          <h3>우리는 계속해서 나아갑니다.</h3>
                          <p>맥도날드는 지난 31년 동안 국내 협력 업체와 긴밀하게 일하고, 현재 15,000여명의</p>
                          <p>직원이 근무하는 등 한국 사회의 고용 창출에 기여하고 있습니다. 또한, 30여개 이상의</p>
                          <p>국내외 비즈니스를 지원해 왔습니다. 맥도날드는 좋은 품질의 제품을 합리적인 가격과</p>
                          <p>최상의 서비스 제공을 통해 고객이 가장 선호하는 장소로 거듭나기 위해 지속적으로</p>
                          <p>노력할 것입니다.</p>
                       </div>
                   </div>
               </div>
           </div>
        </BrandFirstBlock>
    );
};

export default BrandFirst;