import React from 'react';
import styled from 'styled-components';
import {Link} from 'react-router-dom';
import Button from './Button';

const MemberFormBlock = styled.div`
    max-width:450px;
    margin-left:auto; margin-right:auto;
    padding-bottom:100px; 
  
    h3 { text-align:center;
        padding:65px 0;
        font-size:28px; 
    }
`;

const StyledInput = styled.input`
 padding: 15px 10px;
 border:1px solid #555;
 width:100%; 
 margin-bottom:10px; 
`;

const MemberFoot = styled.div`
  margin-top:50px;
  text-align:center;
  a {   
    font-size:14px;
    &:hover { text-decoration:underline; }
}
`;

const textMap = { 
    login:'로그인',
    join:'회원가입'
}


const MemberForm = ({type}) => {
    const text = textMap[type]
    
    return (
        <MemberFormBlock>
            
            <h3>{text}</h3>
            <form>
                {
                    type === 'join' && (
                        <StyledInput name="username" placeholder="이름" type="text" />
                    )
                }
                <StyledInput name="userid" placeholder="아이디" type="text" />
                <StyledInput name="password" placeholder="비밀번호" type="password" />
                {
                    type === 'join' && (
                        <StyledInput name="passwordOk" placeholder="비밀번호 확인" type="password" /> 
                    )
                }
                {
                    type === 'join' && (
                        <StyledInput name="usermail" placeholder="이메일 ex)example@naver.com" type="text" />
                    )
                }
                <Button>{text}</Button>
            </form>
            <MemberFoot>
                {
                    type === 'login' ? (
                        <Link to='/join'>회원가입 바로가기</Link>
                    ) : (
                        <Link to="/login">로그인 바로가기</Link>
                    )
                }
            </MemberFoot>
        </MemberFormBlock>
    );
};

export default MemberForm;