import React from 'react';
import styled, {css} from 'styled-components';
import BrandFirst from './BrandFirst';
import BrandSecond from './BrandSecond';

const BrandTabBlock = styled.div`
   .brandTab { display:flex; justify-content:center;
       
}
`;
const BrandList = styled.div`
   margin:10px 25px; padding:10px 0;
   font-size:18px; font-weight:bold;
   cursor:pointer;
   color:#666;
   ${
       props => props.active && css `color:#ffbc0d; border-bottom:2px solid #ffbc0d `
   }
`;

const brandobj = [
    {id:0, title:"맥도날드 소개", tabCont:<BrandFirst />},
    {id:1, title:"맥도날드 철학 및 역사", tabCont:<BrandSecond />}
]


const BrandTab = ({activeTab, onSelect}) => {
    return (
        <BrandTabBlock>
          <div className="brandTabWrap row">
              <div className="brandTab">
                  {
                      brandobj.map( (data)=>
                          <BrandList key={data.id} active={ activeTab === data.id } onClick={
                              ()=>onSelect(data.id)
                          }>{data.title}</BrandList> )
                  }
              </div>
              <div className="brandContents">
                  {
                      brandobj[activeTab].tabCont
                  }
              </div>
          </div>
        </BrandTabBlock>
    );
};

export default BrandTab;