import React from 'react';
import styled from 'styled-components';

const StoreBgBlock = styled.div`
.storeBg { background-image:url(images/store_bg.jpg);
    background-size:cover;
    background-repeat:no-repeat;
    background-position:center;
    padding:50px 0;
    margin-bottom:50px; 
    .storetxt { color:#fff;
        h2 { font-size:50px; padding:10px 30px; }
    }
}`;

const StoreBg = () => {
    return (
        <StoreBgBlock>
            <div className="storeBg ">
                <div className="storetxt row">
                    <h2>매장찾기</h2>
                </div>
            </div>
        </StoreBgBlock>
    );
};

export default StoreBg;