import React, {useState} from 'react';
import cn from 'classnames';
import './Header.scss';
import {Link} from 'react-router-dom';

const Header = () => {
    
    const [on, setOn] = useState(false); 

    return (
        <div className="headerWrap">
            <div className="row">
                <h1 className="logo">
                    <Link to="/">
                        <img src="images/logo.png" alt="" />
                    </Link>
                </h1>
                <nav className="nav">
                    <ul className="depth1">
                        <li onMouseEnter={()=> setOn(true)} onMouseLeave={()=>setOn(false)}><Link to='/'>메뉴</Link>
                           <ul className={cn("depth2", {on})}>
                               <li><Link to="/burger">햄버거</Link></li>
                               <li><Link to="/dessert">사이드 &amp; 디저트</Link></li>
                           </ul>
                        </li>
                        <li><Link to='/store'>매장찾기</Link></li>
                        <li><Link to='/brand'>브랜드소개</Link></li>
                    </ul>
                </nav>
                <div className="login">
                    <ul>
                        <li><Link to="/login">로그인</Link></li>
                        <li><Link to="/join">회원가입</Link></li>
                    </ul>
                </div>
            </div> 
        </div>
    );
};

export default Header;