import React from 'react';
import styled from 'styled-components';

const BurgerBgBlock = styled.div`
.burgerBg { background-image:url(images/burger_bg.jpg);
    background-size:cover;
    background-repeat:no-repeat;
    background-position:center;
    padding:50px 0;
    margin-bottom:50px; 
    .burgertxt { color:#444;
        h2 { font-size:50px; padding:10px 0; }
        p { font-size:17px; padding:5px 0}
    }
}
`;

const BurgerBg = () => {
    return (
        <BurgerBgBlock>
            <div className="burgerBg ">
                <div className="burgertxt row">
                    <h2>버거</h2>
                    <p>빅맥에서 맥스파이시 상하이버거까지,</p>
                    <p>주문 즉시 바로 조리해 더욱 맛있는, 맥도날드의 다양한 버거를 소개합니다.</p>
                </div>
            </div>
        </BurgerBgBlock>
    );
};

export default BurgerBg;