import React from 'react';
import styled from 'styled-components';

const DessertBgBlock = styled.div`
.dessertBg { background-image:url(images/dessert_bg.jpg);
    background-size:cover;
    background-repeat:no-repeat;
    background-position:center;
    padding:50px 0;
    margin-bottom:50px; 
    .dessert-txt { color:#444;
        h2 { font-size:50px; padding:10px 0; }
        p { font-size:17px; padding:5px 0}
    }
}`;

const DessertBg = () => {
    return (
        
        <DessertBgBlock>
            <div className="dessertBg ">
                <div className="dessert-txt row">
                    <h2>사이드 &amp; 디저트</h2>
                    <p>가볍게 즐겨도, 버거와 함께 푸짐하게 즐겨도,</p>
                    <p>언제나 맛있는 사이드와 디저트 메뉴!</p>
                </div>
            </div>
        </DessertBgBlock>
    
    );
};

export default DessertBg;