import React from 'react';
import styled from 'styled-components';

const StyledButton = styled.button`
   width:100%; 
   padding:15px 0 ;
   background:#b32025; color:#fff;
   border:none;
   transition:all 0.3s;
   &:hover { background:#ffbc0d; }
`;

const Button = (props) => {
    return (
       <StyledButton {...props} />
    );
};

export default Button;