import React from 'react';
import styled from 'styled-components';

const BrandBgBlock = styled.div`
.brandBg { background-image:url(images/brand_topbg.jpg);
    background-size:cover;
    background-repeat:no-repeat;
    background-position:center;
    padding:50px 0;
    margin-bottom:20px; 
    .burgertxt { color:#fff;
        h2 { font-size:48px; padding:10px 0; }
        p { font-size:17px; padding:5px 0}
    }
}
`;

const BrandBg = () => {
    return (
        <BrandBgBlock>
            <div className="brandBg ">
                <div className="burgertxt row">
                    <h2>브랜드 소개</h2>
                    <p>1955년 작은 레스토랑에서부터 지금에 이르기까지 고객이 가장 좋아하는 장소이자,</p>
                    <p>음식을 즐기는 최고의 방법이 되기 위해 맥도날드는 오늘도 노력합니다.</p>
                </div>
            </div>
        </BrandBgBlock>
    );
};

export default BrandBg;